/* quicksand-500 - latin */
@font-face {
  font-family: 'Quicksand';
  font-style: normal;
  font-weight: 500;
  src: url('../fonts/quicksand-v22-latin-500.eot'); /* IE9 Compat Modes */
  src: local(''), url('../fonts/quicksand-v22-latin-500.eot?#iefix') format('embedded-opentype'),
    /* IE6-IE8 */ url('../fonts/quicksand-v22-latin-500.woff2') format('woff2'),
    /* Super Modern Browsers */ url('../fonts/quicksand-v22-latin-500.woff') format('woff'),
    /* Modern Browsers */ url('../fonts/quicksand-v22-latin-500.ttf') format('truetype'),
    /* Safari, Android, iOS */ url('../fonts/quicksand-v22-latin-500.svg#Quicksand') format('svg'); /* Legacy iOS */
  font-display: swap;
}
